<template>
  <v-container class="centered container-reg">
    <v-layout row wrap>
      <!-- Add animal form will only show if the user is logged in -->
      <v-flex xs12 v-if="$store.state.isUserLoggedIn">
        <h1>Add your cute companion to be adopted</h1>
        <input
          class = "changeborder"
          type = "name"
          name = "name"
          v-model="animal.name"
          placeholder = "name"
        />
        <br />
        <br />
        <input
          class = "changeborder"
          type = "species"
          name = "species"
          v-model="animal.species"
          placeholder = "species"
          />
        <br />
        <br />
        <input
          class = "changeborder"
          type = "breed"
          name = "breed"
          v-model="animal.breed"
          placeholder = "breed"
        />
        <br />
        <br />
        <input
          class = "changeborder"
          type = "age"
          name = "age"
          v-model="animal.age"
          placeholder = "age"
        />
        <br />
        <br />
        <input
          class = "changeborder"
          type = "location"
          name = "location"
          v-model="animal.location"
          placeholder = "city"
        />
        <br />
        <br />
        <input
          class = "changeborder"
          type = "shelter"
          name = "shelter"
          v-model="animal.shelter"
          placeholder = "shelter name"
        />
        <br />
        <br />
        <input
          class = "changeborder"
          type = "contact"
          name = "contact"
          v-model="animal.contact"
          placeholder = "shelter email"
        />
        <br />
        <br />
        <textarea
          class = "changeborder"
          rows = "5"
          type = "description"
          name = "description"
          v-model="animal.description"
          placeholder = "description"
        />
        <br />
        <br />
        <input
          class = "changeborder"
          type = "imageURL"
          name = "imageURL"
          v-model="animal.imageURL"
          placeholder = "imageURL"
        />
        <br />
        <br />
        <!-- Shows error message from server if data fails
        Joi authentication -->
        <div class="error" v-html="error"/>
        <v-btn
          flat
          class = "whitetext primary"
          @click="add">
          Add
        </v-btn>
      </v-flex>
      <!-- Message to display if the user is not logged in. -->
      <v-flex xs12 v-if="!$store.state.isUserLoggedIn">
        <h1>Please log in to add an animal!</h1>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import AnimalsService from '@/services/AnimalsService'

export default {
  data () {
    return {
      animal: {
        name: null,
        breed: null,
        age: null,
        location: null,
        shelter: null,
        description: null,
        imageURL: null
      },
      error: null
    }
  },
  methods: {
    async add () {
      try {
        const response = await AnimalsService.post(this.animal) // eslint-disable-line no-unused-vars
        this.$router.push({
          name: 'animals'
        })
      } catch (error) {
        this.error = error.response.data.error
      }
    }
  }
}
</script>



