<template>
  <v-container class="centered container-reg">
    <v-layout row wrap>
      <v-flex xs12>
        <!-- Login form -->
        <h1>Login</h1>
        <input
          class = "changeborder"
          type = "email"
          name = "email"
          v-model="email"
          placeholder = "email"
        />
        <br />
        <br />
        <input
          class = "changeborder"
          type = "password"
          name = "password"
          v-model="password"
          placeholder = "password"
        />
        <br />
        <br />
        <!-- Display error message if login unsuccessful -->
        <div class="error" v-html="error" />
        <br />
        <v-btn flat class = "whitetext primary"
          @click="login">
          Login
        </v-btn>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import AuthenticationService from '@/services/AuthenticationService'
export default {
  data () {
    return {
      email: '',
      password: '',
      error: null
    }
  },
  methods: {
    async login () {
      try {
        /* Send user login data to server to see if it matches
        any registered users. If so, set the token and user in
        the store and return the user to the home page. */
        const response = await AuthenticationService.login({
          email: this.email,
          password: this.password
        })
        this.$store.dispatch('setToken', response.data.token)
        this.$store.dispatch('setUser', response.data.user)
        this.$router.push({
          name: 'home'
        })
      } catch (error) {
        this.error = error.response.data.error
      }
    }
  }
}
</script>



