import axios from 'axios'
import store from '@/store/store' // eslint-disable-line no-unused-vars

export default () => {
  return axios.create({
    baseURL: `https://pleaseadoptme.herokuapp.com`,
    headers: {
      Authorization: `Bearer ${store.state.token}`
    }
  })
}



