<template>
  <v-toolbar flat id="header-bg">
    <!-- SVG logo. Clicking on it sends you to the homepage. -->
    <v-toolbar-title>
      <img id="header-logo" @click="navigateTo({name: 'home'})"
      src="../assets/images/PleaseAdoptMePaw6.svg" />
    </v-toolbar-title>

    <v-spacer></v-spacer>

    <!-- Show Browse, Register and Login links if user is logged out.
    Show drop-down menu if screen is small. -->
    <div v-if="!$store.state.isUserLoggedIn">
    <v-toolbar-items class="hidden-sm-and-down">
      <v-btn
        class="header-btn"
        v-for="item in menuOut"
        :key="item.icon"
        @click="navigateTo({name: item.link}, item.icon)"
        flat>
        {{ item.title }}
      </v-btn>
    </v-toolbar-items>

    <v-menu class="hidden-md-and-up">
      <v-toolbar-side-icon slot="activator"></v-toolbar-side-icon>
      <v-list>
        <v-list-tile v-for="item in menuOut" :key="item.icon">
          <v-list-tile-content>
            <v-list-tile-title @click="navigateTo({name: item.link}, item.icon)">
              {{ item.title }}
            </v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-menu>
    </div>

    <!-- Show Browse, Add Animal, Favourites and Logout links if
    user is logged in. Show drop-down menu if screen is small. -->
    <div v-if="$store.state.isUserLoggedIn">
    <v-toolbar-items class="hidden-sm-and-down">
      <v-btn
      class="header-btn"
      v-for="item in menuIn"
      :key="item.icon"
      @click="navigateTo({name: item.link}, item.icon)"
      flat>
        {{ item.title }}
      </v-btn>
    </v-toolbar-items>

    <v-menu class="hidden-md-and-up">
      <v-toolbar-side-icon slot="activator"></v-toolbar-side-icon>
      <v-list>
        <v-list-tile v-for="item in menuIn" :key="item.icon">
          <v-list-tile-content>
            <v-list-tile-title @click="navigateTo({name: item.link}, item.icon)">
              {{ item.title }}
            </v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-menu>
    </div>
  </v-toolbar>
</template>

<script>
export default {
  data () {
    return {
      /* Define links and routes for the header when the user
      is logged out and logged in */
      menuOut: [
        { icon: 'animals', title: 'Browse', link: 'animals' },
        { icon: 'register', title: 'Register', link: 'register' },
        { icon: 'login', title: 'Login', link: 'login' }
      ],
      menuIn: [
        { icon: 'animals', title: 'Browse', link: 'animals' },
        { icon: 'adddition', title: 'Add Animal', link: 'add-animal' },
        { icon: 'dashboard', title: 'Favourites', link: 'dashboard' },
        { icon: 'logout', title: 'Logout', link: 'home' }
      ]
    }
  },
  methods: {
    menuItems () {
      if ($store.state.isUserLoggedIn) { // eslint-disable-line no-undef
        return this.menuIn
      } else {
        return this.menuOut
      }
    },
    // Logs out the user.
    navigateTo (route, title) {
      if (title === 'logout') {
        this.$store.dispatch('setToken', null)
        this.$store.dispatch('setUser', null)
        this.$router.push({name: 'home'})
      } else {
        this.$router.push(route)
      }
    }
  }
}
</script>



