<template>
  <v-container class="centered container-small" grid-list-md>
    <v-flex xs12>
      <v-toolbar flat id="search-bar">

        <v-spacer></v-spacer>

        <!-- Search box -->
        <v-toolbar-items>
          <input
            class = "changeborder search-box"
            type = "search"
            name = "search"
            v-model = "search"
            placeholder = "enter search term..."
          />
        </v-toolbar-items>
      </v-toolbar>
    </v-flex>

    <!-- Display the image of each animal and their name amd age
    with a CSS overlay encouraging the user to click on the image,
    which redirects them to the individual animal page -->
    <v-layout row wrap>
      <v-flex v-for="animal in animals"
         :key="animal.id" fluid xs12 sm6 md6 lg4>
        <div>
          <v-card class="clickable"
            flat
            @click="navigateTo({
              name: 'animal',
              params: {
                animalId: animal.id
              }
            })">
            <v-img class="browse-image" :src="animal.imageURL" aspect-ratio="1" />
            <div class="overlay"></div>
            <div class="text">
              <p class="browse-whitetext">Click me
                <br />
                to discover more
              </p>
            </div>
          </v-card>
        </div>
        <div class="browse-name">
          <h2>{{animal.name}}</h2>
        </div>
        <div class="browse-age">
          <p>{{animal.age}} years old</p>
        </div>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import AnimalsService from '@/services/AnimalsService'
export default {
  data () {
    return {
      search: '',
      animals: []
    }
  },
  async mounted () {
    // Get the data for all the animals added
    this.animals = (await AnimalsService.index()).data
  },
  methods: {
    // Redirect to individual animal pages
    navigateTo (route) {
      this.$router.push(route)
    }
  },
  watch: {
    // Search box logic
    search (value) {
      const route = {
        name: 'animals'
      }
      if (this.search !== '') {
        route.query = {
          search: this.search
        }
      }
      this.$router.push(route)
    },
    '$route.query.search': {
      immediate: true,
      async handler (value) {
        this.search = value
        this.animals = (await AnimalsService.index(value)).data
      }
    }
  }
}
</script>



