<template>
  <v-container pt-4 class="container-reg">
    <v-layout row wrap>
      <!-- Displays textual information from the database about the selected animal -->
      <v-flex id="information" v-for="animal in animals"
        :key="animal.id" xs12 md4>
        <div class="animal-name">
          {{animal.name}}
        </div>
        <div class="animal-info">
          <b>Age:</b> {{animal.age}} years
          <br />
          <b>Species:</b> {{animal.species}}
          <br />
          <b>Breed:</b> {{animal.breed}}
          <br />
          <b>Location:</b> {{animal.location}}
          <br />
          <b>Shelter:</b> {{animal.shelter}}
          <br />
          <b>Contact email:</b> <i>{{animal.contact}}</i>
          <br />
        </div>
      <br />
      <div class="animal-description">
        {{animal.description}}
      </div>
      <br />

      <!-- Button to opem email form -->
      <!-- Repetition of click event is to stop a bug that
      wouldn't open the form until after two clicks -->
      <v-btn class="black shifted button-pad" @click="openForm()" flat dark>
        <v-icon @click="openForm()" left>email</v-icon>
        <span @click="openForm()">Contact Shelter</span>
      </v-btn>

      <!-- Form content -->
      <div id="myModal" class="modal" v-if="showModal">
        <div class="modal-content modal-pad">
          <div class="modal-header">
            <h1>Contact Shelter</h1>
              </div>
              <div class="modal-body">
                <input
                  class = "changeborder"
                  type = "email"
                  name = "email"
                  placeholder = "Enter email"
                />
              <br/>
              <br/>
              <br/>
              <br/>
              <textarea
                class = "changeborder"
                rows = "10"
                type = "message"
                name = "message"
                placeholder = "Enter message..."
              />
            <br/>
            <br/>
            <v-btn flat class="primary" @click="closeForm()">Send</v-btn>
            <br/>
            <v-btn flat class="primary" @click="closeForm()">Cancel</v-btn>
            </div>
          </div>
        </div>
        <!-- End of form -->

        <!-- If user is logged in and animal is not favourited,
        display favourite button -->
        <v-btn
          v-if="$store.state.isUserLoggedIn && !favourite"
          class="red shifted button-pad"
          flat dark
          @click="setFavourite">
          <v-icon left>favorite</v-icon>
          <span>Favourite</span>
        </v-btn>

        <!-- If user is logged in and animal is favourited,
        display unfavourite button -->
        <v-btn
          v-if="$store.state.isUserLoggedIn && favourite"
          class="blue shifted button-pad"
          flat dark
          @click="undoFavourite">
          <v-icon left>favorite</v-icon>
          <span>Unfavourite</span>
        </v-btn>

        <!-- If user is logged in, show button to delete
        the animal from the database -->
        <v-btn
          v-if="$store.state.isUserLoggedIn"
          class="grey shifted button-pad"
          flat dark
          @click="deleteAnimal">
          <v-icon left>delete</v-icon>
          <span>Delete Animal</span>
        </v-btn>
      </v-flex>

      <!-- For functionality of responsive layout -->
      <v-flex xs12 md1 />

      <!-- Display image of animal from database -->
      <v-flex id="image-col" v-for="animal in animals"
        :key="animal.id" xs8 sm12 md7>
        <v-img class="animal-image" :src="animal.imageURL"/>
        <transition name="bounce"
          @before-enter="beforeEnter"
          @enter="enter"
          @leave="leave">
          <picture class="heart-overlay1" v-if="heartdie">
            <source srcset="../assets/images/heart-medium.png" media="(max-width: 600px)">
            <source srcset="../assets/images/heart-big.png" media="(max-width: 1500px)">
            <source srcset="../assets/images/heart-big.png">
            <img src="../assets/images/heart-big.png" alt="Flowers" style="width:auto;">
          </picture>
        </transition>
      </v-flex>

      <!-- Heart images for use in favourites animation -->
      <div id="heart1">
        <img v-if="show" src="../assets/images/heart-smallish.png" />
      </div>
      <div id="heart2">
        <img v-if="show" src="../assets/images/heart-smallish.png" />
      </div>
      <div id="heart3">
        <img v-if="show" src="../assets/images/heart-smallish.png" />
      </div>
      <div id="heart4">
        <img v-if="show" src="../assets/images/heart-medium.png" />
      </div>
      <div id="heart5">
        <img v-if="show" src="../assets/images/heart-medium.png" />
      </div>
      <div id="heart6">
        <img v-if="show" src="../assets/images/heart-big.png" />
      </div>
    </v-layout>
  </v-container>
</template>

<script>
import AnimalsService from '@/services/AnimalsService'
import FavouritesService from '@/services/FavouritesService'
export default {
  data () {
    return {
      animals: [],
      favourite: false,
      show: false,
      heartdie: false,
      heartBeats: 0,
      showModal: false
    }
  },
  async mounted () {
    // Fetch the animals from the database
    const animalId = this.$store.state.route.params.animalId
    this.animals = (await AnimalsService.show(animalId)).data
    let recaptchaScript = document.createElement('script')
    recaptchaScript.setAttribute('src', 'https://cdnjs.cloudflare.com/ajax/libs/velocity/1.2.3/velocity.min.js')
    document.head.appendChild(recaptchaScript)
  },
  watch: {
    async animals () {
      // If the user is not logged in, no need to get the favourites
      if (!this.$store.state.isUserLoggedIn) {
        return
      }
      try {
        /* Get whether this animal is favourited by the
        current (logged in) user from the database */
        const favourites = (await FavouritesService.index({
          // animals is an array of one so select the first element
          animalId: this.animals[0].id
        })).data
        // If the server returns a favourite, set it
        if (favourites.length) {
          this.favourite = favourites[0]
        }
      } catch (err) {
        console.log(err)
      }
    }
  },
  methods: {
    // Show the contact form pop-up
    openForm () {
      var vm = this
      vm.showModal = true
      var modal = document.getElementById('myModal')
      modal.style.display = 'block'
    },
    // Hide the contact form pop-up
    closeForm () {
      var vm = this
      vm.showModal = false
      document.getElementById('myForm').style.display = 'none'
    },
    // Delete an animal from the database and go back to the main animals page
    async deleteAnimal () {
      try {
        await AnimalsService.delete(this.animals[0].id)
        this.animal = null
        this.$router.push({
          name: 'animals'
        })
      } catch (err) {
        console.log(err)
      }
    },
    async setFavourite () {
      var vm = this
      this.show = true

      // Getting each heart image for animation
      var elem1 = document.getElementById('heart1')
      var elem2 = document.getElementById('heart2')
      var elem3 = document.getElementById('heart3')
      var elem4 = document.getElementById('heart4')
      var elem5 = document.getElementById('heart5')
      var elem6 = document.getElementById('heart6')

      // Getting the screen size and location for responsiveness
      var screenWidth = window.innerWidth
      var screenHeight = window.innerHeight
      var scrollPos = window.scrollY

      // Setting relative positions for each heart image
      var X = screenHeight + scrollPos
      var Y = screenWidth / 2

      var posX1 = X
      var posY1 = Y

      var posX2 = X - 50
      var posY2 = Y - 100

      var posX3 = X + 100
      var posY3 = Y - 170

      var posX4 = X - 100
      var posY4 = Y - 60

      var posX5 = X + 100
      var posY5 = Y - 50

      var posX6 = X + 50
      var posY6 = Y + 100

      // Setting opacity for each heart image
      elem1.style.opacity = 0.8
      elem2.style.opacity = 1
      elem3.style.opacity = 0.9
      elem4.style.opacity = 0.8
      elem5.style.opacity = 1
      elem6.style.opacity = 0.8

      var spaceTravelled = 0

      var id = setInterval(frame, 5)
      function frame () {
        if (spaceTravelled === screenHeight) {
          vm.show = false
          clearInterval(id)
        } else {
          spaceTravelled++

          // Change opacity based on distance travelled
          if (spaceTravelled === screenHeight - 10) {
            elem1.style.opacity = 0.05
            elem2.style.opacity = 0.05
            elem3.style.opacity = 0.05
            elem4.style.opacity = 0.05
            elem5.style.opacity = 0.05
            elem6.style.opacity = 0.05
          } else if (spaceTravelled === screenHeight - 20) {
            elem1.style.opacity = 0.1
            elem2.style.opacity = 0.1
            elem3.style.opacity = 0.1
            elem4.style.opacity = 0.1
            elem5.style.opacity = 0.1
            elem6.style.opacity = 0.1
          } else if (spaceTravelled === screenHeight - 30) {
            elem1.style.opacity = 0.15
            elem2.style.opacity = 0.15
            elem3.style.opacity = 0.15
            elem4.style.opacity = 0.15
            elem5.style.opacity = 0.15
            elem6.style.opacity = 0.15
          } else if (spaceTravelled === screenHeight - 40) {
            elem1.style.opacity = 0.2
            elem2.style.opacity = 0.2
            elem3.style.opacity = 0.2
            elem4.style.opacity = 0.2
            elem5.style.opacity = 0.2
            elem6.style.opacity = 0.2
          } else if (spaceTravelled === screenHeight - 50) {
            elem1.style.opacity = 0.25
            elem2.style.opacity = 0.25
            elem3.style.opacity = 0.25
            elem4.style.opacity = 0.25
            elem5.style.opacity = 0.25
            elem6.style.opacity = 0.25
          } else if (spaceTravelled === screenHeight - 60) {
            elem1.style.opacity = 0.2
            elem2.style.opacity = 0.2
            elem3.style.opacity = 0.2
            elem4.style.opacity = 0.2
            elem5.style.opacity = 0.2
            elem6.style.opacity = 0.2
          } else if (spaceTravelled === screenHeight - 70) {
            elem1.style.opacity = 0.25
            elem2.style.opacity = 0.25
            elem3.style.opacity = 0.25
            elem4.style.opacity = 0.25
            elem5.style.opacity = 0.25
            elem6.style.opacity = 0.25
          } else if (spaceTravelled === screenHeight - 80) {
            elem1.style.opacity = 0.3
            elem2.style.opacity = 0.3
            elem3.style.opacity = 0.3
            elem4.style.opacity = 0.3
            elem5.style.opacity = 0.3
            elem6.style.opacity = 0.3
          } else if (spaceTravelled === screenHeight - 90) {
            elem1.style.opacity = 0.35
            elem2.style.opacity = 0.35
            elem3.style.opacity = 0.35
            elem4.style.opacity = 0.35
            elem5.style.opacity = 0.35
            elem6.style.opacity = 0.35
          } else if (spaceTravelled === screenHeight - 100) {
            elem1.style.opacity = 0.4
            elem2.style.opacity = 0.4
            elem3.style.opacity = 0.4
            elem4.style.opacity = 0.4
            elem5.style.opacity = 0.4
            elem6.style.opacity = 0.4
          } else if (spaceTravelled === screenHeight - 110) {
            elem1.style.opacity = 0.45
            elem2.style.opacity = 0.45
            elem3.style.opacity = 0.45
            elem4.style.opacity = 0.45
            elem5.style.opacity = 0.45
            elem6.style.opacity = 0.45
          } else if (spaceTravelled === screenHeight - 120) {
            elem1.style.opacity = 0.5
            elem2.style.opacity = 0.5
            elem3.style.opacity = 0.5
            elem4.style.opacity = 0.5
            elem5.style.opacity = 0.5
            elem6.style.opacity = 0.5
          } else if (spaceTravelled === screenHeight - 130) {
            elem1.style.opacity = 0.55
            elem2.style.opacity = 0.55
            elem3.style.opacity = 0.55
            elem4.style.opacity = 0.55
            elem5.style.opacity = 0.55
            elem6.style.opacity = 0.55
          } else if (spaceTravelled === screenHeight - 140) {
            elem1.style.opacity = 0.6
            elem2.style.opacity = 0.6
            elem3.style.opacity = 0.6
            elem4.style.opacity = 0.6
            elem5.style.opacity = 0.6
            elem6.style.opacity = 0.6
          } else if (spaceTravelled === screenHeight - 150) {
            elem1.style.opacity = 0.65
            elem2.style.opacity = 0.65
            elem3.style.opacity = 0.65
            elem4.style.opacity = 0.65
            elem5.style.opacity = 0.65
            elem6.style.opacity = 0.65
          }

          // Change position to create animation
          posX1--
          elem1.style.top = posX1 + 'px'
          elem1.style.left = posY1 + 'px'

          posX2--
          elem2.style.top = posX2 + 'px'
          elem2.style.left = posY2 + 'px'

          posX3--
          elem3.style.top = posX3 + 'px'
          elem3.style.left = posY3 + 'px'

          posX4--
          elem4.style.top = posX4 + 'px'
          elem4.style.left = posY4 + 'px'

          posX5--
          elem5.style.top = posX5 + 'px'
          elem5.style.left = posY5 + 'px'

          posX6--
          elem6.style.top = posX6 + 'px'
          elem6.style.left = posY6 + 'px'
        }
      }
      try {
        // Post the favourite for this user and this animal to the database.
        this.favourite = (await FavouritesService.post({
          animalId: this.animals[0].id
        })).data
      } catch (err) {
        console.log(err)
      }
    },
    async undoFavourite () {
      var vm = this
      vm.heartdie = true

      try {
        // Delete the favourite for this user and this animal from the database.
        await FavouritesService.delete(this.favourite.id)
        this.favourite = null
      } catch (err) {
        console.log(err)
      }
    },
    // Rotating heart animation on unfavourite click
    beforeEnter: function (el) {
      el.style.opacity = 0
    },
    enter: function (el, done) {
      var vm = this
      Velocity(el, // eslint-disable-line no-undef
        { opacity: 1 },
        {
          duration: this.fadeInDuration,
          complete: function () {
            done()
            vm.heartdie = false
          }
        }
      )
    },
    leave: function (el, done) {
      var vm = this
      if (vm.heartBeats === 2) {
        Velocity(el, { translateX: '15px', rotateZ: '50deg' }, { duration: 600 }) // eslint-disable-line no-undef
        Velocity(el, { rotateZ: '100deg' }, { loop: 2 }) // eslint-disable-line no-undef
        Velocity(el, { // eslint-disable-line no-undef
          rotateZ: '45deg',
          translateY: '30px',
          translateX: '30px',
          opacity: 0
        }
        )
        vm.heartdie = false
        vm.heartBeats = 0
      } else {
        Velocity(el, // eslint-disable-line no-undef
          { opacity: 0 },
          {
            duration: this.fadeOutDuration,
            complete: function () {
              done()
              vm.heartBeats++
              vm.heartdie = true
            }
          }
        )
      }
    }
  }
}
</script>



