import Api from '@/services/Api'

export default {
  index (favourite) {
    return Api().get('favourites', {
      params: favourite
    })
  },
  post (favourite) {
    return Api().post('favourites', favourite)
  },
  delete (favouriteId) {
    return Api().delete(`favourites/${favouriteId}`)
  }
}



