<template>
  <v-container class="container-reg">
      <!-- Show header only when user is logged in -->
      <h1 v-if='$store.state.isUserLoggedIn'>Favourites</h1>
      <!-- If not logged in, tell the user to log in
      Note that the rest of the page will be blank
      as a user who isn't logged in can't have favourites -->
      <h1 v-if='!$store.state.isUserLoggedIn'>Please login to select your favourite animals!</h1>
      <!-- Display favourites with CSS overlay -->
      <v-layout row wrap>
        <v-flex v-for="favourite in favourites"
          :key="favourite.id" xs6 sm4 md3>
          <v-card class="dash-padding clickable" flat
            @click="navigateTo({
              name: 'animal',
              params: {
                animalId: favourite.AnimalId
              }
            })">
          <v-img class="animal-image" :src="favourite.imageURL" aspect-ratio="1"/>
          <div class="overlay"></div>
          <div class="text">
            <p class="browse-whitetext">{{favourite.name}}
            <br />
            {{favourite.age}} years old</p>
          </div>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import FavouritesService from '@/services/FavouritesService'
export default {
  data () {
    return {
      favourites: []
    }
  },
  // Fetch favourites for this use from database.
  async mounted () {
    if (this.$store.state.isUserLoggedIn) {
      this.favourites = (await FavouritesService.index()).data
    }
  },
  // Navigate to correct individual animal page when image clicked.
  methods: {
    navigateTo (route) {
      this.$router.push(route)
    }
  }
}
</script>



